.home{
    background: url('../../Assets/img/bg.jpg');
    margin-top: 8rem;
    width: 100%;
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    height: 25rem;
    padding-top: 1rem;
     .search{
          margin-left: 10rem;
          height: 2.5rem;
          margin-top: 5rem;
          input{
               left: 10rem;
               width: 75rem;
               height: 2.5rem;
               position: absolute;
               font-size: 1rem;
               padding: 1rem;
               border-radius: 1.5rem;
               background-color: black;
               color: whitesmoke;
               border: 1px;
               outline: none;
          }
          button{
               position: absolute;
               width: 5rem;
               height: 2rem;
               right: 10rem;
               border-radius: 1.5rem;
               margin-top: 0.3rem;
               margin-right: 1.5rem;
               background-color: rgb(64, 130, 215);
               color: whitesmoke;
               border: 1px;
          }
          button:hover{
               background-color: rgb(118, 161, 218);
          }
     }
     p{
          color: #9b9b9b;
          font-size: 0.85rem;
     }
     h1{
          color: #c3c3c3;
    }
     .week{
          width: 80rem;
          margin-left: 8rem;
          height: 15rem;
          margin-top: 10rem;
     }
     .allday{
          width: 80rem;
          display: flex;
          height: 15rem;
          border-radius: 1rem;
          border: 1px solid;
          background-color: #313232;
     }
     .monday{
          width: 20rem;
          border-radius: 1rem;
     } 
     .day{
          background-color: #2b2c2c;
          border-radius:  1rem 0  0;
          display: flex;
          height: 2rem;
          padding-left: 0.5rem;
          align-items: center;
     }
     .date{
          width: 15rem;
          padding: 0.25rem 0.05rem;
          height: 2rem;
          margin-left: auto;
     }
     .number{
          margin-left: 12rem;
          position: relative;
          height: 100%;
     }
     .city{
          margin-left: 1rem;
          margin-top: 1.75rem;
     }
     .city p{
          width: 15rem;
     
     }
     .weathers{
          display: flex;
          width: 20rem;
          height: 5rem;
          margin-top: 0.5rem;
     }
     .temper{
          color: #c3c3c3;
          font-size: 3rem;
          margin-left: 0.5rem;
          margin-top: 0.2rem;
     }
     .weathers img{
          margin-left: 1rem;
          width: 5rem;
          height: 4rem;
     }
     .detail{
          display: flex;
          width: 20rem;
          height: 3rem;
          margin-top: 1rem;
     }
     .east{
          width: 6rem;
          margin-left: .45rem;
          display: flex;
          height: 2rem;
          margin-top: 0.5rem;
     }
     .east img{
          width: 1.5rem;
          height: 1.25rem;
          margin-left: 1rem;
     }
     .east p{
          margin-left: 0.5rem;
          font-size: 0.85rem;
     }
     .weather{
          margin-top: 1rem;
          text-align: center;
          height: 10rem;
     }
     .weather img{
          width: 3rem;
          height: 3rem;
          margin-bottom: auto;
          height: 5rem;
     }
     .days{
          width: 10rem;
     }
     .otherday{
          background-color: #2b2c2c;
          height: 2rem;
          text-align: center;
          padding-top: 0.25rem;
     }
     .otherdaye{
          background-color: #2b2c2c;
          border-radius:   0 1rem 0 0;
          height: 2rem;
          text-align: center;
          padding-top: 0.25rem;
     }
     .dayes{
               background-color: #202121;
               width: 10rem;
     }
     .otherdays{
          background-color: #060606;
          height: 2rem;
          text-align: center;
          padding-top: 0.25rem;
     }

     @media screen and (max-width: 768px){
          .search{
               width: 60%;
               height: 2.5rem;
               margin-top: 5rem;
               background-color: black;
               border-radius: 1.5rem;
               input{
                    width: 55%;
               }
               button{
                    right: 18%;
               }
               button:hover{
                    background-color: rgb(118, 161, 218);
               }
          }
     }
}