.more{
    margin-top: 25rem;
    width: 80rem;
    margin-left: 8rem;
    p{
        font-size: 0.85rem;
        color: #9b9b9b;
        margin-left: 0.25rem;
    }
    .feature{
        font-size: 2rem;
        color: #c3c3c3;
    }
    .contaner{
        width: 80rem;
        height: 25rem;
        display: flex;
    }
    .application{
        width: 25rem
    }
    .row{
        width: 25rem;
        height: 6rem;
        display: flex;
    }
    .row img{
        width: 1rem;
        height: 0.75rem;
        margin-top: 1.5rem;
    }
    .name{
        margin-top: 1rem;
        margin-left: 0.5rem;
    }
    h2{
        font-size: 1rem;
        color: #c3c3c3;
    }
    .analyssis{
        width: 25rem;
        height: 25rem;
    }
    .detail{
        margin-top: 1rem;
    }
    .arrow{
        display: flex;
        width: 25rem;
        height: 1.25rem;
        margin-top: 0.25rem;
    }
    .arrow img{
        width: 1rem;
        height: 0.5rem;
        margin-top: 0.5rem;
    }
    .border_station{
        width: 25rem;
        height: 0.03rem;
        display: inline-block;
        background-color: #1f1f1f;
    }
    .awesome{
        width: 30rem;
        margin-left: auto;
        height: 25rem;
    }
    .photo{
        margin-top: 0.25rem;
    }
    img{
        margin-left: 0.75rem;
        width: 9rem;
        height: 6rem;
    }
}